import React, { useRef, useEffect } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import { Slogan, Center01 } from "../assets/import";
import AnimatedDesc from "../components/AnimatedDesc";
import { useMediaQuery } from "react-responsive";

const About = () => {
  const sectionRef = useRef(null);

  useEffect(() => {
    if (!sectionRef.current) {
      console.warn("sectionRef   .");
    } else {
      console.log("sectionRef :", sectionRef.current.getBoundingClientRect());
    }
  }, []);

  const transition = {
    duration: 2,
    ease: [0.6, -0.05, 0.01, 0.9],
  };

  const textReveal = {
    initial: {
      y: "100%",
      opacity: 0,
    },
    animate: {
      y: "0%",
      opacity: 1,
    },
  };

  const isMobile = useMediaQuery({
    query: "(min-width:600px)",
  });

  return (
    <Wrapper>
      <Desc1
        ref={sectionRef}
        variants={textReveal}
        initial="initial"
        animate="animate"
        transition={transition}
      >
        <MainTxt>Welcome to 1312barre</MainTxt>
        <SubTxt>13(B) 12(R),     ,</SubTxt>
        <SubTxt>1312barre     .</SubTxt>
      </Desc1>

      {/*   Desc  */}
      <Desc>
        <AnimatedDesc>
          <ImgContainer>
            <img src={Slogan} alt="slogan" />
          </ImgContainer>
        </AnimatedDesc>
        <AnimatedDesc>
          <SubTxt>
            Barre with me  ,
            <br />       .
          </SubTxt>
        </AnimatedDesc>
      </Desc>

      {/*   Desc  */}
      <Desc>
        <AnimatedDesc>
          {
            isMobile ? (
              <>
              <SubTxt>
                   .
                <br />
                    , 
                <br />
                    .
                <br />
                      
                <br />
                  
                <br />
                      .
              </SubTxt>
              </>
            ) : (
              <>
              <SubTxt>
                   .
                <br />
                    ,     .
                <br />
                        
                <br />
                      .
              </SubTxt>
              </>
            )
          }
        </AnimatedDesc>
      </Desc>
      <Desc>
        <AnimatedDesc>
          <ImgContainer>
            <img src={Center01} alt="center picture" />
          </ImgContainer>
        </AnimatedDesc>
        <AnimatedDesc>
          <SubTxt>
            1312barre   ,   .
            <br />
                   ,  .
            <br />
            <br />
            <br />
              ,   !
            <br />
            Barre with me!
          </SubTxt>
        </AnimatedDesc>
      </Desc>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  background: #fff;
  position: relative;
  min-height: 200vh; /*     */
  div {
    ${({ theme }) => theme.common.flexCenterColumn};
    width: 100%;
    position: relative;

  }
`;

const Desc1 = styled(motion.div)`
  width: 100%;
  height: 100vh;
`;
const Desc = styled.div`
  width: 100%;
  padding: 100px 0;
  img {
    width: 100%;
  }
`;
const MainTxt = styled(motion.div)`
  font-family: "TANHEADLINE";
  font-size: 7rem;
  font-weight: bold;
  margin-bottom: 30px;
  @media screen and (max-width: 1260px) {
    
  }
  @media screen and (max-width: 1060px) {

  }
  @media screen and (max-width: 600px) {
    text-align: center;
    line-height: 7rem;
    font-size: 5rem;
  }
`;

const SubTxt = styled(motion.div)`
  font-family: "Noto Sans KR", sans-serif;
  text-align: center;
  font-weight: 500;
  font-style: normal;
  font-size: 2rem;
  line-height: 3rem;
  @media screen and (max-width: 1260px) {
    
  }
  @media screen and (max-width: 1060px) {

  }
  @media screen and (max-width: 600px) {
    text-align: center;
    padding: 0 20px;
    width: calc(100% - 40px) !important;
  }
`;

const ImgContainer = styled.div`
  width: 90% !important;
  margin: 100px 50px;
`;
const CenterPic = styled.div``;

export default About;
